import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DeletedManagerComponent } from './deletedManager.component';

const routes: Routes = [
    {
        path: '',
        component: DeletedManagerComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class DeletedManagerRoutingModule {}
